module.exports = ".single-control-container {\n  display: flex;\n  padding-top: 5px;\n  margin-bottom: 10px;\n  width: 100%;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.single-control-lable {\n  color: white;\n  font-family: \"Roboto\", Helvetica, Arial, sans-serif;\n  font-size: 13px;\n}\n\n/* custom css range slider */\n\n.slider {\n  -webkit-appearance: none;\n  width: calc(100% - 10px);\n  height: 20px;\n  border-radius: 20px;\n  padding: 0px 3px;\n  background: #1e1e1e;\n  outline: none;\n  opacity: 0.7;\n  transition: opacity 0.2s;\n}\n\n.slider:hover {\n  opacity: 1;\n}\n\n.slider::-webkit-slider-thumb {\n  -webkit-appearance: none;\n  appearance: none;\n  width: 15px;\n  height: 15px;\n  border-radius: 15px;\n  background: var(--dark-purple);\n  cursor: pointer;\n}\n\n.slider::-moz-range-thumb {\n  width: 15px;\n  height: 15px;\n  border-radius: 15px;\n  background: var(--dark-purple);\n  cursor: pointer;\n}\n\n/* range slider css finish here */\n\n.range-value {\n  padding: 5px;\n  background-color: #1e1e1e;\n  color: white;\n  font-family: \"Roboto\", Helvetica, Arial, sans-serif;\n  border-radius: 5px;\n  margin-left: 5px;\n  font-size: 10px;\n  min-width: 30px;\n  text-align: center;\n}\n/*# sourceMappingURL=data:application/json;base64, */"